import styled from 'styled-components'
import { Link } from 'react-router-dom'
import Slider from 'react-slick'

const Img = styled.img`
  background-image: url(${(props: any) => props.bgUrl});
  display: block;
  /* height: 460px; */
  height: 18%;
  border-radius: 15px;
  justify-content: center;
  margin: 0px auto;
  filter: brightness(70%);
`

const CarouselDiv = styled.div`
  margin: 40px 20px;
  display: flex;
  justify-content: center; /*       */
  align-items: center; /*        */
  &:hover {
    ${Img} {
      filter: brightness(100%);
    }
  }
`

type propTypes = {
  nowPlaying: Array<object>
  isMovie: boolean
}

const Carousel = ({ nowPlaying, isMovie }: propTypes) => {
  const settings = {
    dots: false, //    
    infinite: true, // 
    speed: 1000,
    autoplay: true, //  
    autoplaySpeed: 2000, //  
    slidesToShow: 4, // 4 
    slidesToScroll: 1, // 1  
    centerMode: true,
    centerPadding: '0px', //   padding 0px
  }
  return (
    <Slider {...settings}>
      {nowPlaying.map((movie: any, i: any) => (
        <Link to={isMovie ? `/movie/${movie.id}` : `/show/${movie.id}`}>
          {' '}
          <CarouselDiv key={i}>
            <Img key={i} src={`https://image.tmdb.org/t/p/w300${movie.poster_path}`} />
          </CarouselDiv>
        </Link>
      ))}
    </Slider>
  )
}

export default Carousel
